div.TitleAndControls button
{
    appearance: none;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    fill: white;
}
div.TitleAndControls button:hover
{
    cursor: pointer;
}
div.TitleAndControls button:focus
{
    outline: none;
}

div.TitleAndControls{
    width: 90%;
}

div.album-actions-title {
    color: ivory;
}

div.title * {
    margin: 0px;
}

div.Controls 
{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    color: ivory;
}

div.kkSlider{
    height: 8px;
    overflow: hidden;
    width: 85%;
    flex-shrink: inherit;
    background-color: ivory;
    margin-right: 15px;
}

div.kkCrusin {
    height: 8px;
    flex-shrink: inherit;
    background-color: rgb(114, 114, 114);
    margin-right: 15px;
}